* {
  margin: 0;
  padding: 0;
}

.App {
  text-align: center;
  overflow: hidden;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}

/******************* COMPONENTE NAVEGACION.JS********************/

.titulo-enlace {
  font-size: 1.6vw;
}
.titulo-enlace:hover {
  color: rgba(0, 255, 242, 0.589);
  transition: 0.5s ease-in-out;
}
.container-navegacion {
  width: 100%;
  height: auto;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 15;
  border-bottom: 4px solid rgba(0, 0, 0, 0.288);
}
.container-botones {
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
/******************* COMPONENTE ABOUTME.JS********************/

.container-principal-table {
  width: 100vw;
  height: 100vh;
  position: relative;
}
.container-principal-imagen {
  width: 100%;
  height: 100%;
  display: block;
  background-image: url("./images/banner.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.container-principal-titulo {
  width: 100%;
  height: 100%;
  align-items: center;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
  color: rgb(214, 212, 212);
}
.container-principal-titulo-flex {
  width: 100%;
  height: 100%;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.container-principal-titulo .container-principal-titulo-flex h1 {
  font-size: 6vw;
  font-weight: bold;
  font-family: sans-serif;
}
.container-principal-titulo .container-principal-titulo-flex:hover {
  color: rgba(0, 255, 242, 0.589);
  transition: 0.5s ease-in-out;
  font-weight: 700;
}
.container-principal-titulo .container-principal-titulo-flex:hover > h2 {
  font-size: 4vw;
  transition: 0.8s ease-in-out;
  color: rgba(255, 255, 255, 0.884);
  font-weight: 700;
}

.table {
  margin: 0;
}
.container-aboutMe {
  font-size: 1.2vw;
  text-align: left;
  width: 100%;
  font-family: sans-serif;
  margin: 0 auto;
}
.container-aboutMe h4 {
  font-weight: 600;
  margin: 1vw;
  padding: 0.5vw;
  text-align: center;
  width: 100%;
}
.table-head {
  color: rgba(0, 255, 242, 0.589);
  font-weight: lighter;
  font-size: 1.3vw;
  font-family: sans-serif;
}

/******************* COMPONENTE ESTUDIOS.JS********************/
.container-estudios {
  background-image: url("./images//imgProgramacion.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: auto;
  display: block;
  padding: 1vw;
  margin: 0;
}
.container-tarjet {
  width: 45%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1vw;
  margin: 0 auto;
  margin-bottom: 1vw;
  background-color: rgb(37, 37, 37);
  color: white;
  border-radius: 1vw;
  -webkit-box-shadow: -1px 7px 24px 10px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: -1px 7px 24px 10px rgba(0, 0, 0, 0.75);
  box-shadow: -1px 7px 24px 10px rgba(0, 0, 0, 0.75);
}
.tarjet-title {
  color: rgba(0, 255, 242, 0.589);
}
.container-list {
  border-radius: 1vw;
  border: 2px solid #fff;
}
.item-list:nth-child(even) {
  color: black;
  background-color: #fff;
}
.item-list:nth-child(odd) {
  background-color: rgba(37, 37, 37, 0.582);
  color: rgba(0, 255, 242, 0.589);
}

/******************* COMPONENTE DESCRIPCION.JS********************/

.container-description {
  width: 100%;
  height: auto;
  padding: 3vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgb(0, 121, 115);
}
.container-description .tarjeta-description {
  width: 35%;
  height: auto;
  border-radius: 2vw;
  font-family: sans-serif;
  -webkit-box-shadow: -1px 7px 24px 10px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: -1px 7px 24px 10px rgba(0, 0, 0, 0.75);
  box-shadow: -1px 7px 24px 10px rgba(0, 0, 0, 0.75);
}
.container-description .tarjeta-description .iconoDescription {
  display: inline-block;
  height: 2.2vw;
  margin: 1vw 2vw;
}
.container-description .tarjeta-description .btn-description {
  border-radius: 0.5vw;
  margin-top: 1vw;
  padding: 0.8vw;
}

/******************* COMPONENTE FOOTER.JS********************/

.container-pie-pagina {
  width: 100%;
  background-color: rgb(1, 75, 71);
  border-top: 3px solid rgba(0, 0, 0, 0.288);
  margin: 0;
  padding: 0;
  overflow: hidden;
  font-family: sans-serif;
  font-weight: 600;
  align-items: left;
}
p:first-child {
  text-align: left;
  font-size: 1.1vw;
  color: rgb(37, 37, 37);
}
p:last-child {
  text-align: center;
  color: #fff;
  font-family: sans-serif;
  margin-right: 1vw;
}
.pie-p-container1 {
  width: 20%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.logoPiePagina {
  display: inline-block;
  height: 4vw;
  width: 4vw;
  margin: 1vw 0vw;
  padding: 0;
}

